nav {
  display: flex;
  align-items: center;
  justify-content: space-between;
  background-color: #021630;
  font-family: "Cormorant SC", sans-serif !important;
  position: fixed;
  width: 100%;
}

.nav-logo-container {
  width: 100px;
}

.navbar-menu-container {
  display: none;
}

.nav-link {
  /* Ajoutez ici vos styles personnalisés */
  font-size: 18px; /* Taille de police personnalisée */
  color: #333; /* Couleur de texte personnalisée */
  margin-right: 20px; /* Marge entre les liens */
  /* Autres styles personnalisés */
  cursor: pointer;
}

/* Au survol, vous pouvez ajouter des styles supplémentaires */
.nav-link:hover {
  color: #bababa; /* Couleur de texte au survol */
}

.navbar-links-container a {
  margin-right: 3rem;
  text-decoration: none;
  color: rgb(255, 255, 255);
  font-size: 1.1rem;
  font-weight: 600;
}

.navbar-cart-icon {
  font-size: 1.15rem;
}
.primary-button:hover {
  background-color: rgb(234, 234, 234);
}
.navbar-menu-container svg {
  font-size: 1.5rem;
  cursor: pointer;
  color: white;
  margin-right: 20px;
}

@media (max-width: 850px) {
  .navbar-links-container {
    display: none;
  }
  .navbar-menu-container {
    display: flex;
  }
}

