.produit-container {
  display: flex;
  margin: 100px;
  align-items: center;
}

.produit-container-right {
  margin: 0 100px;
  display: flex;
  align-items: center;
  flex-direction: column;
}
.produit-container-right p {
  margin-top: 15px;
  line-height: 1.3;
  margin-bottom: 50px;
}

.produit-container-right h2 {
  text-align: center;
}

@media (max-width: 1320px) {
  .produit-container {
    display: flex;
    margin-top: 200px;
  }

  .produit-container-right {
    display: flex;
    align-items: center;
    flex-direction: column;
  }
  .produit-container-right p {
    font-size: 18px;
  }

  .produit-container-right h2 {
    font-size: 30px;
  }

  .fourboutton {
    padding: 20px 40px;
    border-radius: 50px;
    font-size: 16px;
  }
}
@media (max-width: 1086px) {
  .produit-container {
    margin-top: 350px;
  }
  .produit-container-right {
    min-width: 300px;
  }
}

@media (max-width: 1070px) {
  .produit-container {
    display: flex;
    flex-direction: column;
    margin-top: 100px;
    align-items: center;
  }
}

@media (max-width: 850px) {
  .produit-container {
    margin-top: 250px;
  }
}

@media (max-width: 440px) {
  .produit-container {
    margin-top: 20px;
    margin-left: 180px;
  }
}

