.solution-container {
  width: 100%;
  height: 1800px;
  background-image: url("../Assets/solution-banner-background.png");
  background-size: cover;
  background-position: center center;
}

.solution-content {
  display: flex;
  text-align: center;
  justify-content: center;
  align-items: center;
  color: #ffffff;
}

.solution-content p {
  font-family: "Cormorant SC" !;
  font-weight: 300;
}
.solution-content h2 {
  margin-bottom: 30px;
}

#icone1,
#icone2,
#icone3,
#icone4 {
  max-width: 100px;
}
.solution-left {
  min-width: 200px;
  max-width: 300px;
  margin: 100px;
}
.solution-left-up {
  margin-bottom: 50px;
}
#telfeedsolution {
  max-width: 400px;
  margin-top: 100px;
}
.solution-right {
  min-width: 200px;
  max-width: 300px;
  margin: 100px;
}

.solution-right-up {
  margin-bottom: 50px;
}

.solution-interaction {
  text-align: center;
  margin: 100px;
  color: white;
}
#solutiontel {
  margin-top: -50px;
}

.solution-interaction p {
  font-size: 33px;
}

@media (max-width: 1320px) {
  /* Solution 3 */
  .solution-container-background {
    width: 1800px;
  }

  .solution-content p {
    font-size: 18px;
    font-weight: 300;
  }
  .solution-content h2 {
    font-size: 25px;
    margin-bottom: 20px;
  }

  .solution-left {
    margin-top: 200px;
  }
  .solution-left-up {
    margin-bottom: 50px;
  }
  #telfeedsolution {
    width: 300px;
  }
  .solution-right {
    margin-top: 200px;
  }

  #icone1,
  #icone2,
  #icone3,
  #icone4 {
    max-width: 100px;
  }

  #solutiontel {
    width: 1000px;
  }

  .solution-interaction p {
    font-size: 26px;
  }
}
@media (max-width: 1234px) {
  .solution-container-background {
    width: 1800px;
  }
  #telfeedsolution {
    display: none;
  }
}
@media (max-width: 1070px) {
  .solution-container-background {
    width: 2000px;
  }
  .solution-content {
    flex-direction: column;
  }
  .solution-left {
    margin: 0px;
    margin-top: 100px;
  }

  .solution-right {
    margin: 0px;
    margin-top: 50px;
  }
  #solutiontel {
    width: 800px;
    margin-top: 20px;
  }
}

@media (max-width: 440px) {
  .solution-container {
    width: 450px;
  }
  
}