/* Générale */

* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  font-family: "Cormorant SC" !;
}

h1 {
  font-size: 52px;
  font-weight: 700;
}

h2 {
  font-size: 35px;
  font-weight: 500;
  font-family: "Cormorant SC" !important;
}

p {
  font-size: 20px;
  font-weight: 300;
}

img {
  max-width: 100%;
}


@media (max-width: 756px) {
  h2 {
    font-size: 24px;
  }
}


