.containerform {
  display: flex;
  width: 100%;
  height: 500px;
  background-image: url("../Assets/contact-banner-background.png");
  background-size: cover;
  background-position: center center;
  justify-content: center;
  align-items: center;
}

.upfoot {
  display: flex;
  justify-content: center;
  align-items: center;
}

.contact-input {
  display: flex;
  flex-direction: column;
  margin: 0 100px;
}
#nom {
  border: none;
  border-radius: 40px;
  padding: 10px;
  width: 250px;
  margin-bottom: 10px;
  font-family: "Cormorant SC" !;
}

#email {
  border: none;
  border-radius: 40px;
  padding: 10px;
  width: 250px;
  font-family: "Cormorant SC" !;
}

.fourboutton {
  padding: 15px 32px;
  background-color: #021630;
  border: none;
  border-radius: 50px;
  font-size: 18px;
  cursor: pointer;
  font-weight: 600;
  color: white;
  transition: 0.2s;
  display: flex;
  align-items: center;
  justify-content: center;
  margin-top: 10px;
  font-family: "Cormorant Sc";
}
#envoyer:hover {
  background-color: #24364c;
}

@media (max-width: 1320px) {
  .upfoot h2 {
    font-size: 26px;
  }

  #nom {
    width: 200px;
  }

  #email {
    width: 200px;
  }
}

@media (max-width: 1070px) {
  .upfoot {
    margin-right: 20px;
    margin-left: 20px;
  }
}

@media (max-width: 756px) {
  .upfoot {
    flex-direction: column;
  }

  .upfoot h2 {
    margin-bottom: 10px;
  }
}

@media (max-width: 440px) {
  .containerform {
    width: 450px;
  }

  .upfoot h2 {
    text-align: center;
  }
}