.about-container {
  display: flex;
  flex-direction: column;
}
.about-container-up {
  display: flex;
  text-align: center;
}
.about-container-up h2 {
  font-weight: 700;
}
.about-left {
  display: flex;
  flex-direction: column;
  margin: 30px;
  align-items: center;
  background: linear-gradient(to bottom, #3a4a5f, #021630);
  padding: 25px;
  border-radius: 60px;
  box-shadow: 10px 10px 20px rgba(0, 0, 0, 0.3);
  color: #ffffff;
}

.about-left h2 {
  margin-top: 10px;
}

.about-left p {
  margin-top: 15px;
  max-width: 300px;
  line-height: 1.3;
}

.about-middle {
  display: flex;
  flex-direction: column;
  margin: 30px;
  align-items: center;
  background: linear-gradient(to bottom, #3a4a5f, #021630);
  padding: 25px;
  border-radius: 60px;
  box-shadow: 10px 10px 20px rgba(0, 0, 0, 0.3);
  color: #ffffff;
}

.about-middle h2 {
  margin-top: 10px;
}

.about-middle p {
  margin-top: 15px;
  max-width: 300px;
  line-height: 1.3;
}

.about-right {
  display: flex;
  flex-direction: column;
  margin: 30px;
  align-items: center;
  background: linear-gradient(to bottom, #3a4a5f, #021630);
  padding: 25px;
  border-radius: 60px;
  box-shadow: 10px 10px 20px rgba(0, 0, 0, 0.3);
  color: #ffffff;
}
.about-right h2 {
  margin-top: 10px;
}
.about-right p {
  margin-top: 15px;
  max-width: 300px;
  line-height: 1.3;
}
.about-container-down {
  margin: 100px;
  text-align: center;
}

.about-container-down p {
  font-size: 33px;
}

@media (max-width: 1320px) {
  /* about */
  .about-container-up img {
    width: 400px;
  }
  .about-container-up h2 {
    font-weight: 700;
    font-size: 30px;
  }
  .about-container-up p {
    font-size: 16px;
  }
  .about-container-down p {
    font-size: 26px;
  }
}
@media (max-width: 1070px) {
  /*  about*/
  .about-container-up {
    display: flex;
    flex-direction: column;
    align-items: center;
  }
  .about-left {
    width: 400px;
  }
  .about-middle {
    width: 400px;
  }
  .about-right {
    width: 400px;
  }
}

@media (max-width: 501px) {
  .about-middle {
    width: 300px;
  }
  .about-left {
    width: 300px;
  }
  .about-right {
    width: 300px;
  }
}

@media (max-width: 501px) {
  .about-container {
    margin-left: 80px;
  }
}
