.footer-wrapper {
  padding: 30px;
  display: flex;
  margin-top: 300px;
  background-color: #021630;
}
.footer-section-one {
  display: flex;
  flex-direction: column;
  align-items: center;
}
.footer-logo-container {
  width: 110px;
  margin-left: -30px;
}
.footer-icons {
  margin-top: 2.5rem;
}
.footer-icons svg {
  font-size: 1.5rem;
  margin-right: 1.25rem;
  color: #ffffff;
}

.footer-section-two {
  flex: 1;
  display: flex;
  justify-content: flex-end;
}
.footer-section-columns {
  display: flex;
  flex-direction: column;
  min-width: 190px;
}
.footer-section-columns span {
  margin: 0.25rem 0rem;
  font-weight: 300;
  color: #ffffff;
  cursor: pointer;
}
.footer-section-columns h2 {
  margin: 0.25rem 0rem;
  font-weight: 300;
  color: #ffffff;
  font-size: 20px;
}
.footer-section-columns p {
  margin: 0.25rem 0rem;
  font-weight: 300;
  color: #ffffff;
  font-size: 16px;
}
#footersolutioncontent p {
  cursor: pointer;
}

@media (max-width: 850px) {
  .footer-wrapper {
    display: flex;
    flex-direction: column;
    align-items: center;
    text-align: center;
  }
  .footer-section-one {
    margin-left: 20px;
  }
  .footer-section-two {
    flex: 1;
    display: flex;
    flex-direction: column;
  }
  .footer-section-two h2 {
    margin-top: 20px;
  }
}

@media (max-width: 440px) {
  .footer-wrapper {
    width: 450px;
  }
}
