.home-container {
  width: 100%;
  height: 780px;
  background-image: url("../Assets/home-banner-background.png");
  background-size: cover;
  background-position: 0 center;
}

.section-1 {
  display: flex;
  align-items: center;
}

.home-text-section {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: flex-start;
  margin-left: 20%;
  margin-top: 50px;
}

.trait {
  width: 250px;
  height: 0.5px;
  border: none;
  background-color: white;
  rotate: 90deg;
  border-radius: 20px;
  margin-top: 50px;
}

.home-feed-tel {
  width: 300px;
  margin-top: 100px;
}

.primary-heading {
  color: white;
  max-width: 506px;
}
.primary-text {
  max-width: 500px;
  color: white;
  margin: 1.5rem 0rem;
}
.home-button {
  display: flex;
}
.secondary-button {
  padding: 20px 50px;
  background-color: #ffffff;
  outline: none;
  border: none;
  border-radius: 50px;
  font-size: 18px;
  cursor: pointer;
  font-weight: 600;
  color: rgb(0, 0, 0);
  transition: 0.2s;
  margin-right: 20px;
  margin-top: 10px;
  font-family: "Cormorant Sc";
}

.secondary-button:hover {
  background-color: rgb(234, 234, 234);
}

.third-button {
  padding: 15px 32px;
  background-color: #021630;
  border: 1px solid white;
  border-radius: 50px;
  font-size: 18px;
  cursor: pointer;
  font-weight: 600;
  color: white;
  transition: 0.2s;
  display: flex;
  align-items: center;
  justify-content: center;
  margin-top: 10px;
  font-family: "Cormorant Sc";
}

.third-button svg {
  margin-left: 10px;
  font-size: 20px;
}
.third-button:hover {
  background-color: #24364c;
}

.video-modal {
  position: fixed;
  display: flex;
  flex-direction: column;
  align-items: center;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  z-index: 9999;
  background-color: rgba(
    202,
    204,
    207,
    0.8
  ); /* Fond assombri avec un filtre transparent léger */
  border-radius: 10px;
  padding: 20px;
  width: 100%;
  overflow-y: auto; /* Permet de faire défiler la vidéo si elle dépasse la hauteur de la fenêtre */
  padding: 200px 0;
}
.close-button {
  background-color: #021630;
  border: none;
  cursor: pointer;
  font-size: 24px;
  color: rgb(255, 255, 255); /* Couleur de l'icône */
  border-radius: 50%; /* Rend le bouton circulaire */
  width: 50px;
  height: 50px;
  display: flex;
  justify-content: center;
  align-items: center;
  border: none;
  margin: 10px;
}

@media (max-width: 1320px) {
  .home-text-section {
    width: 50%;
    margin-top: 150px;
  }
  .home-text-section h1 {
    font-size: 40px;
  }

  .home-text-section p {
    font-size: 20px;
  }

  .trait {
    width: 200px;
    height: 0.5px;
    border: none;
    background-color: white;
    rotate: 90deg;
    border-radius: 20px;
    margin-top: 150px;
  }

  .home-feed-tel {
    width: 250px;
    margin-top: 150px;
    margin-right: 100px;
  }

  .secondary-button {
    padding: 20px 40px;
    border-radius: 50px;
    font-size: 16px;
  }

  .third-button {
    padding: 15px 30px;
    border-radius: 50px;
    font-size: 15px;
  }

  .third-button svg {
    font-size: 17px;
  }
}

@media (max-width: 1086px) {
  .home-text-section h1 {
    font-size: 38px;
  }

  .home-text-section p {
    font-size: 18px;
  }

  .secondary-button {
    padding: 20px 40px;
    border-radius: 50px;
    font-size: 15px;
  }
}

@media (max-width: 1070px) {
  .home-text-section {
    align-items: center;
    justify-content: center;
    margin-left: 25%;
    margin-top: 210px;
  }
  .primary-heading {
    text-align: center;
  }
  .primary-text {
    text-align: center;
  }
  .trait {
    display: none;
  }
  .home-feed-tel {
    display: none;
  }
}

@media (max-width: 756px) {
  .home-text-section {
    margin-top: 160px;
  }
  .home-text-section h1 {
    font-size: 36px;
  }

  .home-text-section p {
    max-width: 300px;
    font-size: 16px;
  }
  .secondary-button {
    padding: 10px 30px;
    font-size: 16px;
  }
  .third-button {
    padding: 7px 20px;
    font-size: 16px;
  }
}

@media (max-width: 700px) {
  .home-text-section {
    margin-top: 180px;
  }
  .home-text-section h1 {
    font-size: 33px;
    width: 400px;
  }
  .home-text-section p {
    max-width: 300px;
    font-size: 16px;
  }
  .secondary-button {
    padding: 10px 30px;
    font-size: 15px;
    width: 150px;
  }
  .third-button {
    padding: 7px 20px;
    font-size: 15px;
    width: 150px;
  }
}

@media (max-width: 501px) {
  .home-text-section {
    margin-top: 180px;
  }
  .home-text-section h1 {
    font-size: 33px;
    width: 400px;
  }
  .home-text-section p {
    max-width: 300px;
    font-size: 15px;
    width: 300px;
  }
  .home-button {
    flex-direction: column;
  }
}

@media (max-width: 440px) {
  .home-container {
    width: 450px;
  }
}
